export default {
  data () {
    return {}
  },
  props: ['color', 'action', 'icon', 'title'],
  computed: {
    dataColor: function () {
      return 'color: #' + this.color
    }
  },
  methods: {
    openWindow () {
      this.$store.dispatch('open' + this.action)
    }
  }
}
