import ws from "../utils/websocket";
export default {
  data() {
    return {
      confirmLogout: true
    };
  },
  methods: {
    closeWindow: function() {
      this.$store.commit("closeSection");
    },
    doLogout: function() {
      ws.closeWebSockets();
      this.$router.push("logout");
    }
  }
};
